* {
  font-family: "Upheaval";
}

img {
  user-select: none;
}

.locationbutton,
.locationbutton-sel,
.locationbutton-inactive {
  //background-color: #000000;
  border-radius: 50%;
  transform: scale(0.67);
  position: absolute;
  width: 30px;
  height: 30px;
}

.locationbutton:hover {
  //background-color: #66ff66;
  transform: scale(1);
}

.locationbutton-sel {
  //background-color: #0099ff;
  transform: scale(1);
}

.locationbutton-inactive {
  //background-color: #ff0000;
}

.locationlabel {
  position: absolute;
  white-space: nowrap;
  left: 50%;
  top: 50%;
  font-size: 30px;
  transform: translate(-50%, -175%);
  user-select: none;
}

.locationmoney {
  position: absolute;
  white-space: nowrap;
  right: 50%;
  top: 250%;
  font-size: 25px;
  transform: translate(50%, -175%);
  user-select: none;
}

.locationmenubutton, .upgradelaunderbutton {
  background-color: #999999;
  user-select: none;
  padding: 5px;
  width: 100px;
  text-align: center;
}

.locationmenubutton:hover, .upgradelaunderbutton:hover {
  background-color: #bbbbbb;
}

.transfermenu {
  position: absolute;
  left: 120%;
}

.upgrademenu {
  position: absolute;
  left: -390%;
}

.status-bar {
  position: absolute;
}

.scorelabel {
  font-size: 30px;
}

.entity {
  position: absolute;
}

.mainmenu {
  position: absolute;
  left: 0px;
  top: 0px;
  right: 0px;
  bottom: 0px;
  justify-content: center;
}

.credits {
  text-align: center;
  margin-top: 80px;
}

.creditsrow {
  font-size: 40px;
  margin-top: 10px;
}

.maintitle {
  text-align: center;
  font-size: 120px;
  width: 100%;
  margin-top: 20px;
  margin-bottom: 20px;
}
.maintitle-sub {
  text-align: center;
  font-size: 40px;
  width: 100%;
  margin-top: 20px;
  margin-bottom: 200px;
}

.mainmenubutton {
  font-size: 60px;
  margin-top: 10px;
  text-align: center;
  color: #0000ff;
}

.mainmenubutton:hover {
  font-size: 70px;
  color: #8888ff;
}

.instructions {
  font-size: 30px;
  text-align: center;
  width: 100%;
  margin-top: 60px;
}

.instructionline {
  position: relative;
  left: 10%;
  width: 80%;
  margin-bottom: 30px;
}

.transfer {
}

.smith {
}

.messagescontainer {
  position: absolute;
  width: 100%;
}

.message,
.map-message {
  position: absolute;
  color: #00ff00;
  z-index: 99;
  user-select: none;
  font-weight: bold;
  -webkit-text-stroke: 1px black;
}

.message {
  font-size: 30px;
}

.map-message {
  font-size: 10px;
}

.audio-controls {
  position: fixed;
  bottom: 0;
  right: 0;
}

.gameover,
.gameover-title,
.gameover-subtitle,
.gameover-score,
.gameover-seized {
  width: 100%;
  text-align: center;
}

.gameover {
  margin-top: 80px;
}

.gameover-title {
  font-size: 100px;
}

.gameover-subtitle {
  font-size: 40px;
}

.gameover-score {
  font-size: 60px;
  margin-top: 100px;
  color: #00ff00;
}

.gameover-seized {
  font-size: 40px;
  color: #ff0000;
}
